.alerta {
  padding: 0.5rem;
  text-align: center;
  color: white;
  font-weight: 700;
  text-transform: uppercase;
  margin: 1rem 0;
}
.alerta.error {
  background-color: rgb(195, 11, 11);
}
.alerta.exito {
  background-color: rgb(3, 144, 3);
}

table.propiedades {
  margin-top: 4rem;
  width: 100%;
  border-spacing: 0;
}
table.propiedades thead {
  background-color: #00569D;
}
table.propiedades thead th {
  color: #FFFFFF;
  padding: 2rem;
}
table.propiedades .imagen-tabla {
  width: 10rem;
}

.imagen-small {
  max-width: 20rem;
}