.alerta {
    padding: .5rem;
    text-align: center;
    color: white;
    font-weight: 700;
    text-transform: uppercase;
    margin: 1rem 0;

    &.error{
        background-color: rgb(195, 11, 11);
    }

    &.exito {
        background-color: rgb(3, 144, 3);
    }
}

table.propiedades {
    margin-top: 4rem;
    width: 100%;
    border-spacing: 0;

    thead {
        background-color: #00569D;

        th {
            color: #FFFFFF;
            padding: 2rem;
        }
    }

    .imagen-tabla {
        width: 10rem;
    }
}

.imagen-small {
    max-width: 20rem;
}